import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DetailComponent } from './detail/detail.component';
import { TimekeepingHistoryComponent } from './timekeeping-history.component'
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
const routes: Routes = [
  {
    path: '',
    component: TimekeepingHistoryComponent
  },
  {
    path: 'detail',
    component: DetailComponent
  }
]

@NgModule({
  declarations: [TimekeepingHistoryComponent, DetailComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    FormsModule,
    ReactiveFormsModule.withConfig({ warnOnNgModelWithFormControl: 'never' }),
    TranslateModule
  ]
})
export class TimekeepingHistoryModule { }
